import React, {useContext}  from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { CurrentEquipContext } from '../../context/currentEquip-context';
import {useCardsStyles} from './CommonCard'
import CardRow, {CardErrorRow} from './CardRow'

const GeneratorCard = React.memo((props) => {
//export default function GeneratorCard() {
  console.log(`! render GeneratorCard`);

  const classes = useCardsStyles();
  const bull = <span className={classes.bullet}>‚Ä¢</span>;

  const generator = props.generator;
  const generatorState = generator?.State;
  const heatStatus = generator?.State?.HeatStatus;
  
  const mas = generatorState?.Post_mas || generatorState?.Mas;
  const kv = generatorState?.Post_kv || generatorState?.Kv;
  const ma = generatorState?.Post_ma || generatorState?.Ma;
  const ms = generatorState?.Post_time || generatorState?.Ms;

  const scopyMas = generatorState?.Scopy_post_mas || generatorState?.Scopy_mas;
  const scopyKv = generatorState?.Scopy_post_kv || generatorState?.Scopy_kv;
  const scopyMa = generatorState?.Scopy_post_ma || generatorState?.Scopy_ma;
  const scopyMs = generatorState?.Scopy_post_ms || generatorState?.Scopy_ms;

  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h5" component="h2">
          {bull}–ì–µ–Ω–µ—Ä–∞—Ç–æ—Ä
        </Typography>
        <CardRow descr="–°–æ—Å—Ç–æ—è–Ω–∏–µ" 
          value={generator?.State?.State > 0? "–ì–æ—Ç–æ–≤" : "–ù–µ –≥–æ—Ç–æ–≤"}
          rightColor={generator?.State?.State > 0? "green" : "red"}
        ></CardRow>
        <CardRow descr="–õ–æ–≥–∏—á–µ—Å–∫–æ–µ. —Ä. –º." value={generator?.State?.Workstation}></CardRow>
        <CardRow descr="–ù–∞–≥—Ä–µ–≤" 
          value={heatStatus === 2 ? "–í—ã—Å–æ–∫–∏–π" : heatStatus === 3 ? "–ö—Ä–∏—Ç–∏—á–µ—Å–∫–∏–π" : heatStatus === 1 ? "–ù–æ—Ä–º" : ""}
          rightColor={heatStatus === 2 ? "yellow" : heatStatus === 3 ? "red" :  heatStatus === 1 ? "green" : "gray"}
        ></CardRow>
        <CardRow descr="–§–æ–∫—É—Å" value={
          generator?.State?.Focus === 1 ? "–ë–æ–ª—å—à–æ–π" : generator?.State?.Focus === 0 ? "–ú–∞–ª—ã–π" : ""
        }></CardRow>
        <CardRow descr="–ü–µ–¥–∞–ª—å" 
          value={ 
            generatorState?.PedalPressed === 1 ? "–ì—Ä–∞—Ñ–∏—è" : 
            generatorState?.PedalPressed === 2 || generatorState?.PedalPressed === 4 ? "–°–∫–æ–ø–∏—è" : 
            generatorState?.PedalPressed === 3 ? "–ì—Ä–∞—Ñ–∏—è –Ω–∞ –∫–æ–ø–∏–∏" :
            "–ù–µ –Ω–∞–∂–∞—Ç–∞"} 
          rightColor={generatorState?.PedalPressed >= 1 && generatorState?.PedalPressed <= 4? "green" : undefined }
        ></CardRow>
        <CardRow descr="–¢–µ—Ö–Ω–∏–∫–∞" value={
          generatorState?.Points_mode === 2 ? "2—Ö —Ç–æ—á–∫–∞" : generatorState?.Points_mode === 3 ? "3—Ö —Ç–æ—á–∫–∞" : ""
        }></CardRow>
        <Typography variant="h6" component="h2">
          {bull}–ì—Ä–∞—Ñ–∏—è
        </Typography>        
        <CardRow descr="–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —ç–ª–µ–∫—Ç—Ä–∏—á–µ—Å—Ç–≤–∞" value={mas ? mas + ' –º–ê—Å' : ''}></CardRow>
        <CardRow descr="–ù–∞–ø—Ä—è–∂–µ–Ω–∏–µ" value={kv ? kv + ' –∫–í' : ''}></CardRow>
        <CardRow descr="–°–∏–ª–∞ —Ç–æ–∫–∞" value={ma ? ma + ' –º–ê' : ''}></CardRow>
        <CardRow descr="–í—Ä–µ–º—è" value={ms ? ms + ' –º—Å' : ''}></CardRow>
        <Typography variant="h6" component="h2">
          {bull}–°–∫–æ–ø–∏—è
        </Typography>
        <CardRow descr="–†–µ–∂–∏–º" value={generatorState?.Scopy_mode}></CardRow>
        <CardRow descr="–ù–∞–ø—Ä—è–∂–µ–Ω–∏–µ" value={scopyKv ? scopyKv + ' –∫–í' : ''}></CardRow>
        <CardRow descr="–°–∏–ª–∞ —Ç–æ–∫–∞" value={scopyMa ? scopyMa + ' –º–ê' : ''}></CardRow>
        <CardRow descr="–í—Ä–µ–º—è" value={scopyMs ? scopyMs + ' –º—Å' : ''}></CardRow>

        <CardRow descr="–û—à–∏–±–∫–∏" value={generator?.State?.ErrorDescriptions?.length}></CardRow>
        {generator?.State?.ErrorDescriptions?.length ? 
          generator.State.ErrorDescriptions.map((i, ind) => (
            <CardErrorRow key={ind.toString()}  descr={i.Code} value={i.Description} ></CardErrorRow>
            ))
            :
            <></>          
        }
      </CardContent>
    </Card>
  );
});

export default GeneratorCard;
